import React from 'react';
import PropTypes from 'prop-types';
import { size, map, get, first, xor, isNumber, isNil } from 'lodash';
import { t } from 'sn-translate';
import makeClass from 'classnames';

import { generateSubComponentIds } from '@devsnc/sn-controls-common';

import OptionDefault from './OptionDefault';

export function AccessibilityDescription({ id, optionsLength }) {
	let accessibilityMessage;

	if (optionsLength === 1) {
		accessibilityMessage = t(
			'1 result. Use arrow up and down keys to select result.'
		);
	} else if (optionsLength > 1) {
		accessibilityMessage = t(
			'{0} results. Use arrow up and down keys to select results.',
			optionsLength
		);
	} else {
		accessibilityMessage = t('0 results.');
	}

	return (
		<span id={id} className="now-a11y-label" aria-atomic aria-live="polite">
			{accessibilityMessage}
		</span>
	);
}

/**
 * options list
 */
class OptionsList extends React.PureComponent {
	constructor(props) {
		super(props);
		this.listItems = [];
	}

	getRef = idx => {
		return node => (this.listItems[idx] = node);
	};

	/**
	 * Scrolls dropdown so that newly selected item is completely in view.
	 */
	componentDidUpdate(prevProps) {
		let newSelected = first(
			xor(this.props.selectedSearchResults, prevProps.selectedSearchResults)
		);
		const targetItem = get(this.listItems, newSelected);

		if (this.list && targetItem) {
			let containerHeight = this.list.getBoundingClientRect().height;
			let containerTop = this.list.scrollTop;
			let containerBottom = containerTop + containerHeight;
			let selectedComponentTop = targetItem.offsetTop;
			let selectedComponentBottom =
				selectedComponentTop + targetItem.offsetHeight;

			if (selectedComponentTop < containerTop) {
				this.list.scrollTop = selectedComponentTop;
			} else if (selectedComponentBottom > containerBottom) {
				this.list.scrollTop = selectedComponentBottom - containerHeight;
			}
		}
	}

	/**
	 * render
	 * @return {ReactElement} markup
	 */
	render() {
		let {
			parentId,
			includeHeaders,
			options,
			recentsCount,
			totalMatchesCount,
			totalMatchesCountHeaderMessage,
			selectedSearchResults,
			onClick,
			onHover,
			currentValues,
			delimiter,
			searchTerm,
			searchUsingStartsWith,
			maxSearchMatches
		} = this.props;

		const optionsLength = size(options);
		const ids = generateSubComponentIds(parentId, { options: optionsLength });
		let totalMatchesCountHeaderMSG;

		if (!isNil(totalMatchesCountHeaderMessage)) {
			totalMatchesCountHeaderMSG = totalMatchesCountHeaderMessage;
		} else if (isNumber(totalMatchesCount)) {
			totalMatchesCountHeaderMSG =
				totalMatchesCount >= maxSearchMatches
					? t(
							'Showing 1-{0} of more than {1}',
							options.length - recentsCount,
							maxSearchMatches
					  )
					: t(
							'Showing 1-{0} of {1}',
							options.length - recentsCount,
							totalMatchesCount
					  );
		} else {
			totalMatchesCountHeaderMSG = t(
				'Showing 1-{0} of {1}',
				options.length - recentsCount,
				options.length
			);
		}

		const headers = {
			0: {
				isEnabled: recentsCount > 0,
				value: t('Recent Selections')
			},

			[recentsCount]: {
				isEnabled: options.length > recentsCount,
				value: totalMatchesCountHeaderMSG
			}
		};

		return (
			<React.Fragment>
				<AccessibilityDescription
					id={`${ids.dropdown}-description`}
					optionsLength={optionsLength}
				/>
				<ul
					id={ids.dropdown}
					className="sn-dropdown-list"
					role="listbox"
					ref="listGroup"
					ref={el => (this.list = el)}
					aria-controls={parentId}
					aria-describedby={`${ids.dropdown}-description`}
				>
					{map(options, (option, idx) => {
						const active = selectedSearchResults.includes(idx);
						const selected = currentValues.includes(option.value);

						const className = makeClass('sn-dropdown-list-item', {
							'is-hover': active,
							'is-hover-disabled': !active,
							'is-selected': selected
						});

						const OptionType = OptionDefault;

						return (
							<React.Fragment key={idx}>
								{includeHeaders && headers[idx] && headers[idx].isEnabled && (
									<li className="sn-dropdown-header">{headers[idx].value}</li>
								)}
								<li
									id={ids.options[idx]}
									className={className}
									role="option"
									ref={this.getRef(idx)}
									aria-selected={selected}
									aria-setsize={optionsLength}
									onClick={() => {
										onClick(option.value, option.displayValue);
									}}
									onMouseEnter={() => onHover(idx)}
								>
									{this.props.checkSelected && (
										<div className="sn-dropdown-list-selected-icon">
											{selected && <now-icon icon="check-fill" size="sm" />}
										</div>
									)}

									<OptionType
										label={option.displayValue}
										secondary={option.secondary}
										value={option.value}
										delimiter={delimiter}
										searchTerm={searchTerm}
										searchUsingStartsWith={searchUsingStartsWith}
									/>
								</li>
							</React.Fragment>
						);
					})}
				</ul>
			</React.Fragment>
		);
	}
}

export default OptionsList;

/**
 * propTypes
 * @type {object}
 * @property {array} options list of options
 * @property {array} selectedSearchResults array of selected indexes
 * @property {function} onClick callback for selecting the option
 * @property {number} recentsCount No. of the recentlty seclected options.
 * @property {number} totalMatchesCount Total no. of options.
 * @property {string} totalMatchesCountHeaderMessage  Header message that'll show up on the total count dropdown group
 * @property {bool} checkSelecte  Whether there will be a checkmark on a selected item
 */
OptionsList.propTypes = {
	includeHeaders: PropTypes.bool,
	options: PropTypes.array.isRequired,
	selectedSearchResults: PropTypes.array.isRequired,
	onClick: PropTypes.func,
	recentsCount: PropTypes.number,
	totalMatchesCount: PropTypes.number,
	totalMatchesCountHeaderMessage: PropTypes.string,
	checkSelected: PropTypes.bool,
	searchUsingStartsWith: PropTypes.bool,
	maxSearchMatches: PropTypes.number
};

OptionsList.defaultProps = {
	includeHeaders: false,
	recentsCount: 0,
	options: [],
	currentValues: [],
	selectedSearchResults: [],
	checkSelected: false
};
