import React from 'react';
import PropTypes from 'prop-types';
import { t } from 'sn-translate';
import { isObject, escapeRegExp } from 'lodash';
import '@servicenow/now-highlighted-value';

export default function OptionUser({
	label,
	secondary,
	value,
	delimiter,
	searchTerm,
	searchUsingStartsWith = true
}) {
	let primaryLabelMatched = false;
	let pre, match, post;
	let escapedSearchTerm = escapeRegExp(searchTerm);
	if (escapedSearchTerm) {
		({ pre, match, post } = getStringParts(label, escapedSearchTerm));
		primaryLabelMatched =
			pre.length === 0 || (!searchUsingStartsWith && match.length >= 1);
	}
	let validSecondary = normalizeSecondaryValues(secondary);
	let secondaryLabelMatched = false;
	return (
		<div className="sn-dropdown-list-item-content">
			<span className="sn-dropdown-list-item-title">
				{primaryLabelMatched ? (
					<span>
						{pre}
						<span className="highlight">{match}</span>
						{post}
					</span>
				) : (
					<span>{label || `(${t('empty')})`}</span>
				)}
			</span>
			{validSecondary &&
				validSecondary.map((secondaryOption, i) => {
					let delim = '';
					let secondaryValue;

					if (!secondaryOption.isHighlighted) {
						if (
							!primaryLabelMatched &&
							escapedSearchTerm &&
							!secondaryLabelMatched
						) {
							secondaryValue = getSecondaryHighlight(
								getStringParts(secondaryOption.value, escapedSearchTerm),
								searchUsingStartsWith
							);
							secondaryLabelMatched = secondaryValue ? true : false;
						}
						if (!secondaryValue)
							secondaryValue = (
								<span className="secondary-value">{secondaryOption.value}</span>
							);
						if (i > 0) {
							delim = (
								<span className="sn-secondary-delimiter">
									{delimiter !== undefined ? delimiter : ' | '}
								</span>
							);
						}
					} else if (secondaryOption.isHighlighted) {
						secondaryValue = (
							<now-highlighted-value
								label={secondaryOption.value}
								status={secondaryOption.status}
								show-icon={secondaryOption.showIcon}
							/>
						);
					}

					return (
						<React.Fragment key={`${value}-secondary-${secondaryOption.value}`}>
							{delim}
							{secondaryValue}
						</React.Fragment>
					);
				})}
		</div>
	);
}

export function normalizeSecondaryValues(secondaryValues = []) {
	return secondaryValues.reduce((normalizedSecondaryValues, secondaryValue) => {
		if (secondaryValue) {
			if (!isObject(secondaryValue)) {
				normalizedSecondaryValues.push({ value: secondaryValue });
			} else {
				normalizedSecondaryValues.push(secondaryValue);
			}
		}
		return normalizedSecondaryValues;
	}, []);
}

export function getSecondaryHighlight(
	{ pre = '', match = '', post = '' },
	searchUsingStartsWith
) {
	if (match == '') {
		return null;
	}

	if (pre.length === 0 || (!searchUsingStartsWith && match.length >= 1)) {
		return (
			<span className="secondary-value">
				{pre}
				<span className="highlight">{match}</span>
				{post}
			</span>
		);
	}
	return null;
}

export function getStringParts(value, searchTerm) {
	let regex = new RegExp(`(.*?)(${searchTerm})(.*)`, 'i');
	let [, pre, match = '', post = ''] = value.match(regex) || ['', value];
	return { pre, match, post };
}

/**
 * propTypes
 * @type {object}
 * @property {string} value value of the option
 * @property {string} label label for the option
 * @property {array} secondary array of secondary labels for the option
 * @property {string} delimiter string delimiter to separate multiple secondary labels
 */
OptionUser.propTypes = {
	value: PropTypes.any,
	label: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	secondary: PropTypes.array,
	delimiter: PropTypes.string,
	searchUsingStartsWith: PropTypes.bool
};
